<template>

	<div class="a1c clearfix">
		<img class="ti" :src="binfo.logo" />
		<div class="tj">
			<div class="a4f clearfix">
				<h2 class="tk">{{binfo.title}}</h2>
			</div>
			<p class="a1d">{{binfo.way}}<span class="line-split"></span><span>{{binfo.time}}</span></p>
			<p class="a1d"><span class="a4t">{{binfo.freight}}</span></p>
		</div>
		<div class="a2o">
			<i class="a2p"></i>
		</div>
		<div class="a3h">
			<ul class="a4h">
				<li class="a3w" v-for="(act,index) in binfo.acts">
					<i class="jx">{{act.type}}</i>
					<span class="du">{{act.cont}}</span>
				</li>
			</ul>
			<div class="a3n">
				<span></span>
				<i class="a3i"></i>
			</div>
		</div>
		<div class="a2n">
			<div class="noticeTips">
				<i></i>
				<div class="tipsAuto">
					<div class="tipsFont">
						{{binfo.notice}}
					</div>
				</div>
			</div>
		</div>
	</div>

</template>

<script>
	
	export default {
		name:'binfo',
		props:['binfo']
	}
	
</script>

<style lang="scss" scoped>
	.a1c {
		position: relative;
		color: #fff;
		min-height: 62px;
		.ti {
			position: absolute;
			left: 2px;
			top: 6px;
			width: 52px;
			height: 53px;
			border-radius: 4px;
			border: 1px solid rgba(255, 255, 255, 0.8);
			background-color: #fff;
		}
		.tj{
			padding-left: 66px;
			line-height: 20px;
			padding-right: 58px;
			min-height: 64px;
			overflow: hidden;
			text-overflow: ellipsis;
			.tk{
				width: 100%;
			    line-height: 28px;
			    font-size: 18px;
			    overflow: hidden;
			    white-space: nowrap;
			    text-overflow: ellipsis;
			    padding-top: 1px;
			}
			.a1d{
				position: relative;
			    line-height: 14px;
			    text-overflow: ellipsis;
			    overflow: hidden;
			    white-space: nowrap;
			    font-size: 10px;
			    .line-split{
			    	padding: 0 2px;
			    	display: inline-block;
				    width: 8px;
				    height: 10px;
				    background: url(../../assets/storeHome1/icon_line_split.jpg) no-repeat center 2px;
			    }
			}
		}
		.a2o{
			position: absolute;
		    top: 6px;
		    right: 2px;
		    width: 40px;
		    height: 26px;
		    font-size: 12px;
		    color: #FFF;
		    text-align: right;
		    .a2p{
		    	float: right;
			    width: 22px;
			    height: 18px;
			    background: url(../../assets/storeHome1/focus.png) no-repeat;
			    background-position: 0px -2px;
			    -webkit-background-size: 60px auto;
			    background-size: 60px auto;
		    }
		}
		.a3h{
			position: relative;
    		padding-top: 14px;
        	color: #666666;
    		font-size: 0px;
    		.a4h{
    			width: 76%;
    			display: inline-block;
    			font-size: 10px;
    			.a3w{
    				position: relative;
				    padding-left: 32px;
				    text-overflow: ellipsis;
				    overflow: hidden;
				    color: #FFF;
				    -webkit-line-clamp: 2;
				    margin-bottom: 8px;
    				margin-top: 0;
    				line-height: 16px;
    				white-space: normal;
    				font-size: 10px;
    				.jx{
    					margin-left: -29px;
    					display: inline-block;
					    padding: 0 2px;
					    margin-right: 4px;
					    border-radius: 2px;
					    color: #ffffff;
					    height: 14px;
					    line-height: 14px;
    				}
    			}
    		}
    		.a3n{
    			position: absolute;
			    bottom: 4px;
			    right: 0;
			    font-size: 11px;
			    width: 64px;
			    padding-left: 6px;
			    line-height: 19px;
			    color: #FFF;
			    background-color: rgba(0,0,0,0.5);
			    border-radius: 2px;
			    .a3i{
			    	width: 8px;
				    height: 8px;
				    margin-left: 4px;
				    display: inline-block;
				    background: url(../../assets/storeHome1/store_sprites_4.6.png) no-repeat;
				    background-size: 220px auto;
			    }
    		}
		}
		.a2n{
			margin-top: 10px;
			.noticeTips{
				line-height: 16px;
    			padding: 0 0 0 3px;
    			i{
    				float: left;
				    display: inline-block;
				    color: #333333;
				    background-color: #fff;
				    font-size: 10px;
				    border: none;
				    padding: 0 2px;
				    border-radius: 2px;
				    height: 14px;
				    line-height: 14px;
				    margin-top: 2px;
    			}
    			.tipsAuto{
    				margin-bottom: 5px;
    				padding: 0 26px 0 37px;
    				.tipsFont{
    					color: #fff;
					    font-size: 10px;
					    text-overflow: ellipsis;
					    overflow: hidden;
    				}
    			}
			}
		}
	}
</style>


