<template>
	<li class="dj">
		<i :class="className">{{voucher}}</i>
		<span class="di">{{fullcut}}</span>
	</li>
</template>

<script>
	export default {
		name: 'discount',
		props: ['className','voucher', 'fullcut']
	}
</script>

<style lang="scss">
	.dj {
		margin-top: 10px;
		font-size: 10px;
		position: relative;
		color: #999999;
		-webkit-text-size-adjust: none;
		text-overflow: ellipsis;
		white-space: nowrap;
		overflow: hidden;
		i {
			display: inline-block;
			padding: 0 2px;
			margin-right: 4px;
			border-radius: 2px;
			color: #ffffff;
			height: 14px;
			line-height: 14px;
			background-color: #fdc44c;
		}
		.ia{
			background: #fa3e2d;
		}
		.ib{
			background: #5FBC65;
		}
		.ic{
			background: #72bb00;
		}
		.ie{
			background: #5CA7FF;
		}
		.if{
			background: #FF6C57;
		}
	}
</style>


