<template>

	<section class="sec4">

		<article class="scrolling">
			<div class="content scroller">
				<div class="x6" style="min-height: 667px;">
					<div class="sg">
						<p></p>
						<router-link class="wr" to="/login"></router-link>
					</div>
				</div>
			</div>
		</article>

		<fot :className="className"></fot>

	</section>

</template>

<script>
	
	import fot from './footer';
	
	export default {
		name: 'orderList',
		components:{
  			fot
  		},
		data:function (){
			return {
				className:'orderList'
			}
		}
	}
</script>

<style lang="scss">
	.sec4 {
		.scrolling{
			webkit-overflow-scrolling: touch;
			overflow: auto;
		    position: absolute;
		    left: 0;
		    right: 0;
		    top: 0;
		    bottom: 0;
		    .scroller{
		    	overflow: auto;
		    	height: 100%;
		    	.x6{
		    		background: url(../assets/unlogin.png) center no-repeat;
		    		background-size: 96px;
				    -webkit-transform: translateY(-90px);
				    transform: translateY(-90px);
				    position: relative;
				    .sg{
				    	position: absolute;
					    top: 50%;
					    padding-top: 63px;
					    text-align: center;
					    width: 100%;
					    font-size: 15px;
					    line-height: 1;
					    color: #666;
					    .wr{
					    	display: inline-block;
						    margin-top: 8px;
						    padding: 10px 8px;
						    border-radius: 3px;
						    border: 1px solid #47b34f;
						    color: #47b34f;
						    background: #fff;
						    line-height: 1;
					    }
				    }
		    	}
		    }
		}
		.o {
			position: fixed;
			z-index: 801;
			left: 0;
			right: 0;
			bottom: 0;
			width: 100%;
			height: 50px;
			background: #fff;
			border-top: 1px solid #d9d9d9;
			font-size: 0;
			a {
				display: inline-block;
				vertical-align: top;
				width: 20%;
				font-size: 12px;
				line-height: 17px;
				text-align: center;
				padding-top: 30px;
				height: 20px;
				background-image: url(../assets/nav4.png);
				background-repeat: no-repeat;
				background-size: 24px 600px;
			}
			.index {
				background-position: center -250px;
			}
			.classify {
				background-position: center -300px;
			}
			.carList {
				background-position: center -350px;
			}
			.orderList {
				background-position: center -150px;
				color: #39ac69;
			}
			.mine {
				background-position: center -450px;
			}
		}
	}
</style>


