<template>
	
	<div class="storeCou">
		<div class="a1f">
			<h2 class="a1e"></h2>
		</div>
		<div class="a1m">
			<div class="a1n">
				<ul class="a2x">
					<li class="clearfix" v-for="(bcoupon,index) in bcoupons">
						<div class="ada">
							<div class="ad3">
								<div class="a31">
									<div class="a4s">
										<div class="ad4">{{bcoupon.much}}</div>
										<div class="ad5">{{bcoupon.continued}}</div>
									</div>
								</div>
							</div>
							<div class="a32">
								<span class="a3j">{{bcoupon.total}}</span>
								<span class="a3m">{{bcoupon.type}}</span>
							</div>
						</div>
						<div class="a30">
							<div class="a2z">
								<div class="a3u"></div>
							</div>
						</div>
					</li>
				</ul>
			</div>
		</div>
	</div>
	
</template>

<script>
	
	export default {
		name:'bcoupon',
		props:['bcoupons']
	}
	
</script>
 
<style lang="scss" scoped>
	.a1f{
		position: relative;
    	margin-bottom: 20px;
    	.a1e{
    		position: relative;
		    width: 32%;
		    margin: 0 auto;
		    font-size: 15px;
		    line-height: 18px;
		    color: #FFF;
		    text-align: center;
    	}
	}
	.a1n{
		margin-bottom: 6px;
		.a2x{
			width: auto;
		    white-space: nowrap;
		    border: 1px solid #f8f8f8;
		    border-width: 0;
		    li{
		    	position: relative;
			    height: 70px;
			    margin: 9px;
			    border-radius: 5px;
			    overflow: hidden;
			    border-left: 3px solid #ff5757;
			    -webkit-box-sizing: border-box;
			    box-sizing: border-box;
			    .ada{
			    	float: left;
				    height: 100%;
				    width: 100%;
				    .ad3{
				    	float: left;
					    position: relative;
					    width: 100%;
					    height: 100%;
					    .a31{
					    	position: relative;
						    margin-left: 60px;
						    margin-right: 71px;
						    height: 100%;
						    background-color: #fffbfb;
						    border-top: 1px solid #ff9d9d;
						    border-bottom: 1px solid #ff9d9d;
						    -webkit-box-sizing: border-box;
						    -moz-box-sizing: border-box;
						    box-sizing: border-box;
						    .a4s{
						    	position: absolute;
							    left: 6px;
							    right: 0;
							    top: 50%;
							    -webkit-transform: translate(0,-50%);
							    transform: translate(0,-50%);
							    text-align: left;
							    .ad4{
							    	margin-bottom: 4px;
								    color: #333;
								    text-overflow: ellipsis;
								    overflow: hidden;
								    font-size: 14px;
								    white-space: nowrap;
							    }
							    .ad5{
							    	margin-bottom: 3px;
								    font-size: 11px;
								    color: #666;
								    overflow: hidden;
								    text-overflow: ellipsis;
								    white-space: nowrap;
							    }
						    }
					    }
				    }
				    .a32{
				    	float: left;
					    width: 60px;
					    height: 100%;
					    position: relative;
					    text-align: center;
					    background-color: #fffbfb;
					    line-height: 70px;
					    margin-left: -100%;
					    border-top: 1px solid #ff9d9d;
					    border-bottom: 1px solid #ff9d9d;
					    -webkit-box-sizing: border-box;
					    box-sizing: border-box;
					    .a3j{
					    	font-size: 20px;
						    color: #ff4f64;
						    font-family: Arial,'Microsoft YaHei',Verdana,Arial,Helvetica,sans-serif;
					    }
					    .a3m{
					    	margin-left: -4px;
						    font-size: 15px;
						    color: #ff4f64;
					    }
				    }
			    }
			    .a30{
			    	float: left;
				    position: relative;
				    width: 61px;
				    height: 100%;
				    margin-left: -71px;
				    background: url(../../assets/storeHome1/store_act_new_conpon.png) no-repeat;
				    background-position: 0 0;
				    background-size: 10px 100%;
				    padding-left: 10px;
				    .a2z{
				    	width: 100%;
					    height: 100%;
					    border-top: 1px solid #FFF;
					    border-top-right-radius: 5px;
					    border-bottom-right-radius: 5px;
					    border-top-left-radius: 1px;
					    border-bottom-left-radius: 1px;
					    background-color: #fffbfb;
					    border: 1px solid #ff9d9d;
					    border-left: none;
					    -webkit-box-sizing: border-box;
					    -moz-box-sizing: border-box;
					    box-sizing: border-box;
					    .a3u{
					    	display: block;
						    width: 50px;
						    height: 20px;
						    font-size: 13px;
						    color: #fefffe;
						    text-align: center;
						    line-height: 20px;
						    margin: 24px auto 0;
						    background-color: #ff5757;
						    -webkit-border-radius: 20px;
						    -moz-border-radius: 20px;
						    border-radius: 20px;
					    }
				    }
				    .a2z::before{
				    	content: " ";
					    position: absolute;
					    width: 57px;
					    height: 38px;
					    top: -2px;
					    right: 4px;
					    background-repeat: no-repeat;
					    background-size: 57px auto;
				    }
			    }
		    }
		}
	}
	
	.a1f::before{
		content: '';
		position: absolute;
		top: 9px;
		left: 0;
		right: 64%;
		border-top: 1px solid rgba(255,255,255,0.4);
	}
	
	
</style>


