import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import classify from '@/components/classify'
import carList from '@/components/carList'
import orderList from '@/components/orderList'
import mine from '@/components/mine'
import login from '@/components/login'
import storeHome from '@/components/storeHome'

Vue.use(Router)

export default new Router({
	mode: 'hash',
  routes: [
	{ path: "/", redirect: "/index" },
    {
      path: '/index',
      name: 'index',
      component: index
    },
    {
        path: '/classify',
        name: 'classify',
        component: classify
     },
     {
         path: '/carList',
         name: 'carList',
         component: carList
     },
     {
          path: '/orderList',
          name: 'orderList',
          component: orderList
      },    
      {
           path: '/mine',
           name: 'mine',
           component: mine
      },
      {
          path: '/storeHome:id',
          name: 'storeHome',
          component: storeHome
     },
      {
          path: '/login',
          name: 'login',
          component: login
     }
  ]
})



