<template>
	<div>
		
		<header id="header">
			<span class="icon icon-goback" @click="back"></span>
			<span class="txt-header"></span>
		</header>
		
		<section class="page">
			
			<div class="login-tab">
				<ul class="username-login J_ping login-selected" @click="showuser">
					<span id="account_login_txt" :class="{redbot:yes == true}"></span>
				</ul>
				<ul class="sms-login J_ping" @click="showsms">
					<span id="sms_login_txt" :class="{redbot:yes == false}"></span>
				</ul>
			</div>
			
			<div class="wrap loginPage">
				
				<!---->
				<div id="username_login" class="login-wrap login-wrap-active" v-show="yes">
					<div class="input-container">
						<div class="txt-username"></div>
						<input type="text" id="username" ref="userInput" class="acc-input username txt-input J_ping" placeholder="//">
						<i class="icon icon-clear" style="display: none;"></i>
					</div>
					<div class="input-container">
						<div class="txt-username"></div>
						<input type="password" id="username" ref="pwdInput" class="acc-input username txt-input J_ping" placeholder="">
						<i class="icon icon-clear" style="display: none;"></i>
						<label class="label-checkbox J_ping">
							<input type="checkbox" id="onOff_pwd">
							<div class="checkbtn"></div>
						</label>
					</div>
					<div id="input-code" class="input-container" style="display: none;">
						<div class="txt-imgverify"></div>
						<input id="code" class="acc-input imgverify J_ping" type="text" placeholder="" autocomplete="off">
						<span class="code-box"><img id="imgCode" src="" alt="" class="J_ping"></span>
						<i class="icon icon-clear"></i>
					</div>
				</div>
				
				<!---->
				<div class="login-wrap" id="sms_login" v-show="!yes">
					<div id="img_code_box" class="input-container" style="display: none;">
						<input class="acc-input sms-img-verify J_ping" id="smsCode" type="text" placeholder="">
						<span class="sms-code-box"><img id="smsImgCode" src="" class="J_ping"></span>
						<i class="icon icon-clear"></i>
					</div>
					<div class="sms-input-box">
						<div class="input-container">
							<label class="area-box J_ping">
		                        <span class="area_code" code="86">+86</span>
		                        <i class="area_icon"></i>
		                    </label>
							<input type="tel" id="telphone" class="acc-input telphone sms-txt-input J_ping" placeholder="">
							<i class="icon icon-clear"></i>
						</div>
						<button data-mescode="true" class="mesg-code mesg-disable J_ping"></button>
					</div>
					<div class="input-container">
						<input id="telCode" class="acc-input telCode sms-txt-input J_ping" type="text" placeholder="">
						<i class="icon icon-clear"></i>
					</div>
				</div>
				
				<div class="notice">&nbsp;</div>
				<a href="javascript:;" id="loginBtn" class="btn J_ping" @click="sendInfo" style="display:block;"> </a>
				<a href="javascript:;" id="loginOneStep" class="btn btn-onestep J_ping" style="display:block;"></a>
				<div class="quick-nav clearfix" style="display:;">
					<a href="javascript:;" class="J_ping findpwd">
						<i class="icon icon-clock"></i><span class="txt-findpwd"></span>
					</a>
					<a href="javascript:;" class="J_ping quickReg">
						<i class="icon icon-reg"></i><span class="txt-quickReg"></span>
					</a>
				</div>
				
				<div class="quick-login" style="display:;">
					<h4 class="txt-otherLogin"></h4>
					<a href="" class="J_ping quick-qq">
						<i class="icon icon-qq"></i>
					</a>
					<a href="" class="J_ping quick-wx" style="display:">
						<i class="icon icon-wx"></i>
					</a>
					<a href="" class="J_ping quick-money" style="display:none">
						<i class="icon icon-money"></i>
					</a>
					<div class="agreement-tips">
						<p>
							<a href=""></a>
						</p>
					</div>
				</div>
				
			</div>
		</section>
	</div>
</template>

<script>
	export default {
		name: 'login',
		data (){
			return {
				yes:true
			}
		},
		methods:{
			back:function (){
				this.$router.back();
			},
			showuser:function (){
				this.yes=true;
			},
			showsms:function (){
				this.yes=false;
			},
			sendInfo:function (){
				var userName = this.$refs.userInput.value;
				var pattenPwd = this.$refs.pwdInput.value;
			}
		}
	}
</script>

<style lang="scss" scoped>
	*{
		font-size:0.28rem;
		text-align: center;
	}
	
	input {
		border-radius: 0;
		border: 0;
		display: inline-block;
		box-sizing: border-box;
		font-family: PingFang-SC-Regular;
	}
	
	img {
		border: 0;
		vertical-align: middle;
		-ms-interpolation-mode: bicubic;
	}
	
	.btn,
	input {
		font-size: .28rem;
		outline: 0;
	}
	
	header {
		height: 0.9rem;
		line-height: 0.9rem;
		width: 100%;
		background: #f9f9f9;
		color: #616161;
		font-size: 0.34rem;
		border-bottom: 1px solid #bbb;
		z-index: 999;
		box-sizing: border-box;
		text-align: center;
		.icon-goback {
			top: 0.24rem;
			left: 0.24rem;
			background-position: 0 -1.26rem;
		}
	}
	
	.page {
		background: white;
		overflow-y: auto;
		width: 100%;
		box-sizing: border-box;
		.login-tab {
			line-height: 0.88rem;
			font-size: 0.28rem;
			border-bottom: 1px solid #d7d7d7;
			.username-login {
				width: 50%;
				display: inline-block;
				float: left;
			}
			.redbot {
				border-bottom: 2px solid #f23030;
				padding: 0.26rem 0.2rem;
			}
			.sms-login {
				width: 50%;
				display: inline-block;
			}
		}
		.wrap {
			max-width: 15rem;
			margin: 0 auto;
			padding: 0 0.4rem 0.4rem 0.4rem;
			.login-wrap-active {
				display: block;
			}
			.input-container {
				position: relative;
				width: 100%;
				height: 0.96rem;
				line-height: 0.96rem;
				overflow: hidden;
				padding: 0.1rem;
				background-color: #fff;
				border-bottom: 1px solid #d7d7d7;
				-moz-box-sizing: border-box;
				box-sizing: border-box;
				div {
					position: absolute;
					width: 1.2rem;
					display: inline-block;
					font-family: PingFang-SC-Regular;
					text-align: left;
				}
				.username {
					width: 100%;
					padding: 0 0.6rem 0 1.2rem;
				}
			}
			.icon-clear {
				top: 0.26rem;
				right: 0.2rem;
				background-position: 0 0;
			}
			.password {
				width: 100%;
				padding: 0 1.4rem 0 1.2rem;
				.icon-clear {
					right: 0.9rem;
				}
			}
			.imgverify {
				width: 100%;
				padding: 0 1.6rem 0 1.2rem;
			}
			.code-box {
				width: 1.5rem;
				height: .46rem;
				right: 0.1rem;
				top: 0.25rem;
				border-left: 1px solid #d7d7d7;
				position: absolute;
				img {
					width: 1.5rem;
					height: .6rem;
					margin-top: -0.56rem;
					border: 1px solid transparent;
				}
				.icon-clear {
					right: 1.7rem;
				}
			}
			.sms-img-verify {
				width: 100%;
				padding: 0 1.6rem 0 0;
			}
			.sms-code-box {
				position: absolute;
				width: 1.8rem;
				height: .46rem;
				right: 0;
				top: 0.25rem;
				border-left: 1px solid #d7d7d7;
				img {
					width: 1.6rem;
					height: .6rem;
					margin-top: -0.56rem;
					border: 1px solid transparent;
				}
				.icon-clear {
					right: 1.84rem;
				}
			}
			.sms-input-box {
				position: relative;
				.area-box {
					position: absolute;
					width: 1.24rem;
					text-align: right;
					.area_icon {
						width: 0;
						height: 0;
						border-left: 0.08rem solid transparent;
						border-right: 0.08rem solid transparent;
						border-top: 0.1rem solid #ABABAB;
						display: inline-block;
						margin: 0 0.14rem 0 0.04rem;
						padding-top: 1px;
					}
				}
				.telphone {
					width: 100%;
					padding-left: 1.2rem;
					padding-right: 2.6rem;
					.icon-clear {
						right: 2.36rem;
					}
				}
				.mesg-code {
					width: 2.36rem;
					border-left: 1px solid #d7d7d7;
					padding: 0;
					height: .46rem;
					line-height: .46rem;
					top: 0.25rem;
					outline: 0;
					background-color: #fff;
					-webkit-box-shadow: none;
					box-shadow: none;
				}
				.mesg-disable {
					color: #848689;
				}
			}
			.telCode {
				width: 100%;
				padding-right: .64rem;
				padding-left: 0;
			}
			.notice {
				font-size: .24rem;
				line-height: .6rem;
				color: #f23030;
				min-height: .3rem;
				text-align: left;
			}
			.btn,
			.btn:active,
			.btn:hover,
			.btn:visited {
				color: #848689;
			}
			.btn-onestep:active,
			.btn-onestep:hover,
			.btn-onestep:visited {
				color: #f23030!important;
			}
			.btn {
				// height: .9rem;
				line-height: .92rem;
				background-color: #eee;
				border-radius: 0.04rem;
				border: 0;
			}
			.btn-onestep {
				border: 1px solid #f23030;
				color: #f23030;
				background: #fff;
				margin-top: .2rem;
			}
			.clearfix:after,
			.clearfix:before {
				content: "";
				display: table;
			}
			.clearfix:after {
				clear: both;
			}
			.quick-nav {
				margin-top: .4rem;
				text-align: center;
				a:first-child {
					float: left;
				}
				a {
					color: #333;
					text-indent: .5rem;
					line-height: .44rem;
					font-family: PingFang-SC-Regular;
				}
				a:last-child {
					float: right;
				}
			}
			.quick-login {
				margin-top: 2.1rem;
				height: 1.7rem;
				position: relative;
				padding: 0.49rem .2rem 0 .2rem;
				border-top: 1px solid #d7d7d7;
				h4 {
					position: absolute;
					top: -0.38rem;
					left: 50%;
					background-color: white;
					padding: 0.1rem .3rem;
					color: #bfbfbf;
					-webkit-transform: translateX(-50%);
					transform: translateX(-50%);
					font-size: .28rem;
				}
				a {
					position: relative;
					width: 1.44rem;
					color: #616161;
					margin: 0 .2rem;
					font-size: .28rem;
					display: inline-block;
					.icon-qq {
						left: .5rem;
						background-position: 0 -2.58rem;
						width: .44rem;
						height: .44rem;
					}
					.icon-wx {
						left: .5rem;
						background-position: 0 -3.1rem;
						width: .44rem;
						height: .44rem;
					}
				}
				.agreement-tips {
					position: absolute;
					bottom: 0;
					left: 0;
					width: 100%;
					height: .6rem;
					font-size: .28rem;
					line-height: .6rem;
					a {
						width: auto;
						margin: 0;
						color: #333;
						text-decoration: underline;
					}
				}
			}
		}
	}
	
	.icon {
		width: .44rem;
		height: .44rem;
		overflow: hidden;
		position: absolute;
		background-image: url(../assets/login/iconBg.png);
		background-repeat: no-repeat;
		background-size: 100% auto;
		display: block;
	}
	
	.icon-clock {
		background-position: 0 -0.46rem;
	}
	
	.icon-reg {
		background-position: 0 -0.84rem;
	}
	
	.quick-login a,
	.quick-nav a {
		display: inline-block;
		font-size: .24rem;
	}
	
	.username::-webkit-input-placeholder {
		color: #d7d7d7;
	}
	
	.mesg-code {
		position: absolute;
		width: 2.6rem;
		height: .86rem;
		line-height: .86rem;
		right: 0;
		top: 0;
		background-color: #fff;
		font-size: .28rem;
		border: 0;
		outline: 0;
		border-radius: 0.04rem;
		-webkit-box-shadow: 0 0 0 1px #f23030;
		box-shadow: 0 0 0 1px #f23030;
	}
	
	.btn,
	.mesg-code,
	.pop-btns a,
	.quick-login,
	.quick-login a,
	header {
		text-align: center;
	}
	
	.label-checkbox {
		position: absolute;
		right: 0.1rem;
		top: .3rem;
		height: .36rem;
		width: .6rem;
		overflow: hidden;
		z-index: 99;
		input {
			display: none;
		}
		.checkbtn {
			display: block!important;
			width: .6rem!important;
			height: .36rem!important;
			background-image: url(../assets/login/hide_password.png);
			background-repeat: no-repeat;
			background-size: 100% auto;
			background-clip: padding-box;
			color: #fff;
			-webkit-box-sizing: border-box;
			-moz-box-sizing: border-box;
			box-sizing: border-box;
		}
	}
</style>


