	    padding-bottom: 0;
	    padding-top: 0;
	    margin-bottom: 10px;
	    margin-top: -1px;
	    background-color: rgba(255,255,255,0.1);
	    border-radius: 3px;
	    overflow: hidden;
	}
	.a3a{
		padding-bottom: 10px;
	    padding-top: 12px;
	    overflow: hidden;
	}
	.a1h{
		padding-top: 20px;
	    padding-bottom: 5px;
	    color: #FFF;
	    line-height: 24px;
	}
	.a2f{
		padding-top: 12px;
