    	font-size: 12px;
    	background: url(../../assets/storeHome1/right_arrow.png) right 5px no-repeat;
    	background-size: 5px auto;
	}
	.a2h{
		display: block;
		text-align: right;
		color: #fff;
	}
	.a16{
		background: url(../../assets/storeHome1/store_sprites_4.6.png) no-repeat;
		background-size: 220px auto;
	}
</style>


