<template>

	<section class="sec3">

		<article class="scrolling">
			<div class="content">
				<div class="a4v" style="background-color: rgb(255,255,255);">
					<table class="c" style="background-color: #fff;z-index: 5;">
						<tbody>
							<tr>
								<th>
									<div class="a57">
										~
										<br />
										
										<br />
										<router-link class="a58" to="/login"></router-link>
									</div>
								</th>
							</tr>
						</tbody>
					</table>
				</div>
			</div>
		</article>

		<fot :className="className"></fot>

	</section>

</template>

<script>
	
	import fot from './footer';
	
	export default {
		name: 'carList',
		components:{
  			fot
  		},
		data:function (){
			return {
				className:'carList'
			}
		}
	}
</script>

<style lang="scss">
	.sec3 {
		.scrolling{
			.content{
				.a4v{
					padding: 0 0 99px 0;
					text-align: center;
					background-color: #f4f4f4;
					.c{
					    table-layout: fixed;
					    border-collapse: collapse;
					    position: fixed;
					    z-index: 2000;
					    left: 0;
					    top: 0;
					    right: 0;
					    bottom: 0;
					    width: 100%;
					    height: 100%;
					    background-color: rgba(0,0,0,.8);
					    .a57{
					    	padding: 100px 0 160px 0;
					    	line-height: 20px;
					    	color: #666;
					    	text-align: center;
					    	font-size: 14px;
					    	background: url(../assets/unlogin.png) center top no-repeat;
					    	background-size: 96px 96px;
					    	.a58{
					    		display: inline-block;
							    vertical-align: top;
							    padding: 0 8px;
							    line-height: 35px;
							    color: #fff;
							    background-color: #47b34f;
							    color: #fff;
							    font-size: 15px;
							    margin-top: 16px;
							    border-radius: 3px;
					    	}
					    }
					}
				}
			}
		}
		.o {
			position: fixed;
			z-index: 801;
			left: 0;
			right: 0;
			bottom: 0;
			width: 100%;
			height: 50px;
			background: #fff;
			border-top: 1px solid #d9d9d9;
			font-size: 0;
			.index {
				background-position: center -250px;
			}
			.classify {
				background-position: center -300px;
			}
			.carList {
				background-position: center -100px;
				color: #39ac69;
			}
			.orderList {
				background-position: center -400px;
			}
			.mine {
				background-position: center -450px;
			}
		}
	}
</style>


