<template>

	<a class="a9d">
		<span class="d0">
			<img :src="imgurl"/>
			<span class="cz">{{dtext}}</span>
		</span>
		<span class="d1">{{dprice}}</span>
	</a>

</template>

<script>
	export default {
		name: 'selling',
		props: ['imgurl','dtext','dprice']
	}
</script>

<style lang="scss">
	.a9d {
		position: relative;
		display: inline-block;
		overflow: hidden;
		width: 25%;
		text-align: center;
		font-size: 0;
		vertical-align: top;
		max-height: 105px;
		.d0 {
			position: relative;
			display: block;
			width: 60px;
			height: 60px;
			border: 1px solid #f7f7f7;
			border-radius: 3px;
			img {
				display: inline-block;
				width: 100%;
			}
			.cz {
				position: absolute;
				left: 0;
				top: 0;
				padding: 1px 2px;
				font-size: 9px;
				color: #FFF;
				background-color: #fc2c50;
			}
		}
		.d1 {
			font-size: 12px;
			color: #70704e;
			height: 17px;
			line-height: 17px;
			vertical-align: top;
			display: block;
			width: 62px;
			overflow: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;
			color: #ff5757;
			margin-top: 3px;
		}
	}
</style>


