<template>

	<a :class="anclass">
		<strong class="ak" v-if="see">
		<em class="al">{{texto}}</em><span class="am">{{textt}}</span>
		</strong>
		<img :class="inclass" :src="imgurl" />
		<span :class="spclass" v-if="seee">{{texth}}</span> {{text}}
	</a>

</template>

<script>
	export default {
		name: 'aimg',
		props: ['anclass', 'see', 'texto', 'textt', 'inclass', 'imgurl','spclass', 'seee', 'texth', 'text']
	}
</script>

<style lang="scss">
	.ad {
		color: #000000;
		width: 20%;
		display: inline-block;
		vertical-align: top;
		line-height: 20px;
		font-size: 12px;
		padding: 20px 0 13px;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	
	.ae {
		width: 70%;
		margin: 0 auto;
		display: block;
	}
	
	.ah {
		display: block;
		height: 100%;
		position: relative;
		line-height: 0.48rem;
		text-align: center;
		z-index: 6;
		.ak {
			float: none;
			text-align: center;
			width: auto;
			.al {
				display: block;
				font-size: 0.28rem;
				padding-top: 0.2rem;
				line-height: 0.3rem;
				height: 0.3rem;
				font-weight: bold;
				text-overflow: ellipsis;
			}
			.am {
				display: block;
				height: 0.44rem;
				font-size: 0.22rem;
				text-overflow: ellipsis;
				color: #9cac96;
			}
		}
		.ai {
			width: 1.1rem;
			height: 1.1rem;
			margin: 0 auto;
			display: block;
		}
	}
	
	.t6 {
		display: inline-block;
		padding: 25px 0;
		width: 25%;
		text-align: center;
		line-height: 35px;
		position: relative;
		color: #323232;
		font-size: 13px;
		border-bottom: 1px solid #e8e8e8;
	}
	
	.t6:last-child {
		border-bottom: none;
	}
	
	.t6:nth-last-child(2) {
		border-bottom: none;
	}
	
	.t6m {
		background-size: 25px 25px;
		display: block;
		width: 25px;
		height: 25px;
		margin: 0 auto;
	}
	
	.vx {
		display: inline-block;
		width: 33%;
		min-height: 94px;
		margin-top: 6px;
		.vy {
			display: block;
			width: 62px;
			height: 62px;
			margin: 0 auto;
		}
		.vz {
			display: block;
			width: 100%;
			text-align: center;
			font-size: 12px;
			color: #7d7d7d;
			padding: 8px 0;
		}
	}
</style>


