<template>
	
	<a class="a9j">
		<span class="a9l clearfix">
			<img class="d9" :src="imgurl"/>
		</span>
	</a>
	
</template>

<script>
	
	export default {
	    name:'logo',
	    props:['imgurl']
	}
	
</script>

<style lang="scss">
	
	.a9j{
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		float: none;
		.a9l{
			position: relative;
			display: block;
			.d9{
				float: left;
				width: 64px;
				height: 64px;
				border: 1px solid #e8e8e8;
				border-radius: 4px;
				display: inline-block;
				margin-right: 8px;
			}
		}
	}
	
</style>


