<template>
	
	<section class="sec2">
		
		<!---->
		<div class="vc">
			<div class="vi">
				<i class="vj"></i><input type="search" class="vp" id="" placeholder="" />
			</div>
		</div>
		
		<!---->
		<ul class="vq">
			<li v-for="(item,index) in rough" class="vs" :class="{vt:index == num}" @click="toggle(index)" :key="index">
				{{item}}
			</li>
		</ul>
		
		<!---->
		<article class="hasSearch">
			<div class="content">
				<goodz v-for="(good,index) in goods" :good="good" :key="index" v-if="index==num" ></goodz>
			</div>
		</article>
		
		<!---->
		<fot :className="className"></fot>
		
	</section>
	
</template>

<script>
	
	/**/
	import $ from 'jquery/dist/jquery.min.js';
	
	/**/
	import goodz from './classify/goods';
	import fot from './footer';
	
	export default {
  		name: 'classify',
  		components:{
  			goodz,
  			fot
  		},
  		created(){
			var classifyUrl='https://easy-mock.com/mock/5a051583034de12ca203602f/jdata/classify';
			var th=this;
			$.get(classifyUrl,function (res){
				for(let i=0;i<res.good.length;i++){
					th.goods.push(res.good[i]);
				}
			});
		},
  		methods:{
  			toggle(index) {
				this.num = index;
			}
  		},
  		data:function (){
  			return {
  				num: 0,
  				className:'classify',
  				rough:['','','','','','','','','',''],
  				goods:[]
  			}
  		}
	}
	
</script>

<style lang="scss">
	
	@import '../assets/css/classify.scss';
	
</style>


