
import Vue from 'vue';
import App from './App'
import router from './router';
import '../static/css/reset.css';
import '../static/js/size.js';
import 'swiper/dist/css/swiper.min.css';
import store from 'store/dist/store.everything.min.js';

new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App },
  data:{
  	bus:new Vue()
  },
  created() {
    if (!store.get('car')){
      store.set('car', {});
    }
  }
})



