<template>
	<div :class="className">
		<ul class="swiper-wrapper swiper1list">
			<li class="swiper-slide swiper1item" v-for="(item,index) in carousel" :key="index">
				<aimg :imgurl="item.imgurl"></aimg>
			</li>
		</ul>
		<div class="swiper-pagination"></div>
	</div>
</template>

<script>
	
	import aimg from './aimg';

	export default {
		name: 'carousel',
		components: {
			aimg
		},
		props: ['carousel','className']
	};
</script>

<style lang="scss">
	.b2 {
	  margin-top: 0;
	}
	.a9 {
	  min-height: 120px;
	}
	.t4{
		width: 100%;
		height: 75px;
	}
	.swiper7 {
		position: relative;
		z-index: 8;
		white-space: nowrap;
		text-align: left;
		overflow: hidden;
		.swiper1list {
			position: relative;
			.swiper1item {
				position: relative;
				display: inline-block;
				vertical-align: top;
				width: 100%;
				text-align: center;
				a {
					display: block;
					width: 100%;
					img {
						display: block;
						width: 100%;
					}
				}
			}
		}
	}
	.c1{
		background-color: #fff;
	    position: relative;
	    margin: 2% 10px 2% 10px !important;
	}
	.swiper1{}
	.swiper2{}
	.swiper3{}
	.swiper4{}
	.swiper5{}
</style>


