<template>

	<div class="r8">
		<div class="a9k">
			<logo :imgurl="store.basic.imgurl"></logo>
			<div class="d7">
				<info :title="store.basic.title" :honors="store.basic.honors" :dada="store.basic.dada" :time="store.basic.time" :number="store.basic.number" :service="store.basic.service" :much="store.basic.much" :distance="store.basic.distance" :freight="store.basic.freight">
				</info>
				<div v-if="store.discounts">
					<div class="a9f">
						<div class="a1a">
							<a class="dh">
								<ul class="a9i">
									<discount v-for="(discount,index) in store.discounts" v-if="index < num" :className="discount.className" :voucher="discount.voucher" :fullcut="discount.fullcut" :key="index"></discount>
								</ul>
							</a>
							<div class="w4" v-if="store.discounts.length > 2" @click.stop="show()">
								<i class="a9g a1b" :class="{ac7:num==100}"></i>
							</div>
						</div>
						<div class="a9c" v-if="store.sellings">
							<selling v-for="(selling,index) in store.sellings" :imgurl="selling.imgurl" :dtext="selling.dtext" :dprice="selling.dprice" :key="index"></selling>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>

</template>

<script type="text/javascript">
	import logo from "./logo";
	import info from "./info";
	import discount from "./discount";
	import selling from "./selling";

	export default {
		name: 'stores',
		components: {
			logo,
			info,
			discount,
			selling
		},
		data: function() {
			return {
				yes: true,
				num: 2
			}
		},
		props: ['store'],
		methods: {
			show: function() {
				if(this.yes) {
					this.num = 100;
					this.yes = !this.yes;
				} else {
					this.num = 2;
					this.yes = !this.yes;
				}
			}
		}
	}
</script>

<style lang="scss">
	.r8 {
		position: relative;
		font-size: 0;
		min-height: 55px;
		.a9k {
			position: relative;
			.d7 {
				font-size: 12px;
				color: #999;
				margin-left: 76px;
				.a9f {
					position: relative;
					margin-top: 12px;
					padding-bottom: 0;
					.a1a {
						position: relative;
						color: #666666;
						font-size: 0px;
						.dh {
							display: inline-block;
							width: 86%;
							font-size: 10px;
						}
						.w4 {
							position: absolute;
							top: 3px;
							right: -10px;
							width: 14%;
							font-size: 10px;
							vertical-align: top;
							text-align: right;
							padding-top: 24px;
							height: 40px;
							margin-top: -16px;
							padding-right: 10px;
							.a9g {
								background: url(../../assets/img/index/index.png) no-repeat;
								background-size: 20px 200px
							}
							.a1b {
								display: inline-block;
								width: 18px;
								height: 10px;
								text-align: right;
								background-position: 2px -183px;
							}
							.ac7 {
								background-position: 2px -163px;
							}
						}
					}
				}
				.a9c {
					font-size: 0;
					vertical-align: top;
					white-space: nowrap;
					padding-top: 12px;
				}
			}
		}
	}
</style>


