<template>

	<li class="a1t a1x" :class="{promotion:indexs<3}" @click="show2(indexs)">
		<strong class="a1w">
			<em class="a1u a16" v-if="indexs<3"></em>
			<var class="a22">{{good.title}}</var>
		</strong>
		<strong v-if="good.lists" class="a1z" v-show="indexs==num">
			<span v-for="(list,index) in good.lists" :key="index" class="a1y">{{list}}</span>
		</strong>
	</li>

</template>

<script>
	export default {
		name: 'hlists',
		props: ['good','indexs'],
		data:function (){
			return {
				num:100
			}
		},
		methods:{
			show2:function (indexs){
				if(this.num==100){
					this.num=indexs;
				}else{
					this.num=100;
				}
			},
		}
	}
</script>

<style lang="scss">
	.a1t {
		color: #666666;
		font-size: 13px;
	}
	.a1x {
		.active {
			background: #fff;
			color: #47b34f;
		}
		.a1y {
			font-size: 12px;
			color: #666;
			background-color: #FFF;
			padding-top: 1px;
		}
	}
	.promotion {
		.a1w {
			position: relative;
			padding-left: 24px;
			width: 56px;
		}
		.a1u {
			position: absolute;
			left: 0px;
			top: 12px;
			width: 24px;
			height: 22px;
			background-position: -10px -187px;
		}
	}
	.a1w{
		display: -webkit-box;
	    display: -moz-box;
	    width: 70px;
	    height: 46px;
	    padding-left: 10px;
	    padding-right: 4px;
	    line-height: 20px;
	    background-color: #f4f4f4;
	    text-overflow: ellipsis;
	    overflow: hidden;
	    -webkit-line-clamp: 2;
	    -webkit-box-orient: vertical;
	    -moz-line-clamp: 2;
	    -moz-box-orient: vertical;
	    -webkit-box-pack: center;
	    -moz-box-pack: center;
	    box-pack: center;
	}
	
	.a16 {
		background: url(../../assets/storeHome1/store_sprites_4.6.png) no-repeat;
		background-repeat: no-repeat;
		background-size: 220px auto;
	}
	
	.a22 {
		display: block;
	}
	
	.a1z {
		display: block;
		overflow: hidden;
		position: relative;
	}
	
	.a1y {
		display: -webkit-box;
		display: -moz-box;
		width: 70px;
		height: 46px;
		padding-left: 10px;
		padding-right: 4px;
		line-height: 20px;
		overflow: hidden;
		text-overflow: ellipsis;
		-webkit-line-clamp: 2;
		-moz-line-clamp: 2;
		-webkit-box-orient: vertical;
		-moz-box-orient: vertical;
		-webkit-box-pack: center;
		font-size: 12px;
		padding-top: 1px;
		position: relative;
	}
	
</style>


