<template>

	<a class="a98">
		<div class="a9n clearfix">
			<h2>{{title}}</h2>
			<div v-for="(honor,index) in honors" class="honor-box" ><span class="honor-icon" :class="honor"></span></div>
		</div>
		<div class="a9o">
			<strong class="i9">
				<span class="da">{{dada}}</span>
				<span class="db">-</span>
				<span class=" aaa">{{time}}</span>
			</strong>
		</div>
		<div class="a9s">
			<p class="a9a">
				<span class="a7o clearfix">
					<i></i>
					<i></i>
					<i></i>
					<i></i>
					<i></i>
				</span>
			</p>
			<p class="a9t"><span class="split"></span>{{number}}</p>
		</div>
		<span class="uh">{{much}}{{distance}}km,{{freight}}</span>
	</a>

</template>

<script>
	export default {
		name: 'info',
		props: ['title','honors','dada','time','number','much','distance','freight']
	}
</script>

<style lang="scss">
	.a98 {
		display: block;
		min-height: 66px;
		.a9n {
			height: 20px;
			overflow: hidden;
			display: -webkit-box;
			display: -moz-box;
			display: -ms-box;
			display: flex;
			-moz-flex: 1;
			-webkit-flex: 1;
			-ms-flex: 1;
			flex: 1;
			h2 {
				font-size: 16px;
				color: #333;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
				display: inline-block;
				margin-top: 0;
				font-weight: bold;
				-webkit-flex: 1;
				-moz-flex: 1;
				-ms-flex: 1;
				flex: 1;
			}
			.honor-box {
				padding-top: 1px;
				.honor-icon {
					display: inline-block;
					width: 18px;
					height: 20px;
					margin-right: 2px;
					background-position: center center;
					background-repeat: no-repeat;
					background-size: 16px auto;
				}
			}
		}
		.a9o{
			font-size: 0;
			padding-top: 10px;
			overflow: hidden;
			.i9{
				display: inline-block;
			    border: 1px solid #16A9ff;
			    font-size: 0;
			    border-radius: 2px;
			    color: #16a9ff;
			    line-height: 0;
			    height: 15px;
			    overflow: hidden;
			    .da{
					display: inline-block;
					font-size: 10px;
					padding: 2px;
					line-height: 1;
				}
				.db{
					line-height: 1;
    				font-size: 10px;
				}
				.aaa{
					display: inline-block;
				    padding: 2px;
				    line-height: 1;
				    font-size: 10px;
				}
			}
		}
		.a9s{
			height: 14px;
			margin-top: 8px;
			overflow: hidden;
			font-size: 0;
			.a9a{
				height: 12px;
				padding-top: 2px;
				overflow: hidden;
				display: inline-block;
				position: relative;
				.a7o{
					display: inline-block;
					font-size: 0;
					i{
						display: inline-block;
					    width: 10px;
					    height: 10px;
					    background-position: 0 0;
					    background-image: url(../../assets/img/index/newStar.png);
					    background-repeat: no-repeat;
					    background-size: auto 10px;
					    margin-right: 1px;
					}
				}
			}
			.a9t{
				font-size: 11px;
			    line-height: 14px;
			    overflow: hidden;
			    text-overflow: ellipsis;
			    white-space: nowrap;
			    color: #999;
			    display: inline-block;
			    .split{
			    	display: inline-block;
				    width: 8px;
				    height: 10px;
				    background: url(../../assets/img/index/icon_line_split.jpg) no-repeat center 2px;
			    }
			}
		}
		.uh{
			display: block;
		    margin-top: 8px;
		    font-size: 11px;
		    color: #999999;
		    overflow: hidden;
		    white-space: nowrap;
		    text-overflow: ellipsis;
		}
	}
	.honor1{
		background-image: url(../../assets/img/index/honor1.png);
	}
	.honor2{
		background-image: url(../../assets/img/index/honor2.png);
	}
	.honor3{
		background-image: url(../../assets/img/index/honor3.png);
	}
</style>


