<template>
	
	<div class="a1g a1i">
		<div class="a1f">
			<h2 class="a1e"></h2>
		</div>
		<ul class="a19 a2">
			<li>
				<div class="a2 commentWarp clearfix">
					<dl class="scoreWarp a2">
						<dt>{{evaluate.score}}</dt>
						<dd></dd>
					</dl>
					<dl class="numberWarp">
						<dt>{{evaluate.conumber}}</dt>
						<dd></dd>
					</dl>
				</div>
			</li>
			<li class="a1j clearfix">
				<ul class="evalaut">
					<li class="list-li">
						<div class="headerpic">
							<img class="pic" src="/static/storeHome1/evaluateHeader.jpg"/>
						</div>
						<div class="evalautlist">
							<dt>
								<div class="pName">{{evaluate.pname}}</div>
								<span class="Datatime">{{evaluate.ptime}}</span>
							</dt>
							<dd>
								<span class="aa2 clearfix"><i></i></span>
							</dd>
						</div>
					</li>
				</ul>
			</li>
			<li class="a4j a2 clearfix"><a class="Allcomments"></a></li>
		</ul>
	</div>
	
</template>

<script>
	
	export default {
		name:'evaluate',
		props:['evaluate']
	}
	
</script>

<style lang="scss" scoped>
	.a1i{
		padding-bottom: 0;
	}
	.a1g{
		padding-top: 25px;
		padding-bottom: 35px;
		color: #fff;
	}
	.a1f{
		position: relative;
    	margin-bottom: 20px;
	}
	.a1f::before{
		content: '';
	    position: absolute;
	    top: 9px;
	    left: 0;
	    right: 64%;
	    border-top: 1px solid rgba(255,255,255,0.4);
	}
	.a1e{
		position: relative;
	    width: 32%;
	    margin: 0 auto;
	    font-size: 15px;
	    line-height: 18px;
	    color: #FFF;
	    text-align: center;
	}
	.a19{
		padding: 0;
	    margin: 0;
	    padding: 10px;
	    padding-bottom: 0;
	    padding-top: 0;
	    margin-bottom: 10px;
	    margin-top: -1px;
	    background-color: rgba(255,255,255,0.1);
	    border-radius: 3px;
	    overflow: hidden;
	}
	.a2{
		position: relative;
	}
	.commentWarp{
		padding: 15px 0;
    	color: #fff;
    	dl{
    		float: left;
		    width: 50%;
		    box-sizing: border-box;
		    dt{
		    	text-align: center;
			    font-size: 27px;
			    font-weight: bold;
			    height: 36px;
			    line-height: 36px;
			    overflow: hidden;
		    }
		    dd{
		    	overflow: hidden;
			    text-align: center;
			    font-size: 12px;
			    height: 18px;
			    line-height: 18px;
		    }
    	}
	}
	.a1j{
		padding-bottom: 10px;
	    padding-top: 20px;
	    color: #FFF;
	}
	.aa2{
		display: inline-block;
		font-size: 0;
		i{
			display: inline-block;
		    width: 10px;
		    height: 10px;
		    background: url(../../assets/storeHome1/newStar.png) no-repeat;
		    background-position: 0 0;
		    background-size: auto 10px;
    		margin-right: 1px;
		}
	}
	.evalaut{
		overflow: hidden;
		.list-li{
			overflow: hidden;
    		position: relative;
    		.headerpic{
    			float: left;
    			.pic{
    				width: 35px;
				    height: 35px;
				    display: inline-block;
				    -webkit-border-radius: 18px;
				    -ms-border-radius: 18px;
				    -moz-border-radius: 18px;
				    border-radius: 18px;
				    float: left;
    			}
    		}
    		.evalautlist{
    			margin-left: 40px;
    			color: #fff;
    			dt{
    				line-height: 16px;
    				position: relative;
    			}
    			.pName{
    				margin-right: 130px;
				    text-overflow: ellipsis;
				    white-space: nowrap;
				    overflow: hidden;
				    font-size: 14px;
    			}
    			.Datatime{
    				position: absolute;
				    right: 0;
				    top: 0;
				    font-size: 12px;
				    line-height: 16px;
    			}
    			dd{
    				padding-top: 5px;
    				overflow: hidden;
    			}
    		}
		}
	}
	.Allcomments{
		    display: block;
		    height: 46px;
		    line-height: 46px;
		    text-align: center;
		    font-size: 14px;
		    color: #fff;
	}
</style>


