<template>

	<footer :class="'o '+className">
		<router-link v-for="(addr,index) in addrs" :class="addr.className" :to="addr.to" :key="index">{{addr.text}}</router-link>
	</footer>

</template>

<script>
	export default {
		name: 'fot',
		props: ['className'],
		data: function() {
			return {
				addrs:[
					{text:'',className:'index',to:'/'},
					{text:'',className:'classify',to:'/classify'},
					{text:'',className:'carList',to:'/carList'},
					{text:'',className:'orderList',to:'/orderList'},
					{text:'',className:'mine',to:'/mine'}
				]
			}
		}
	}
</script>

<style lang="scss">
	.o {
		position: fixed;
		z-index: 801;
		left: 0;
		right: 0;
		bottom: 0;
		width: 100%;
		height: 50px;
		background: #fff;
		border-top: 1px solid #d9d9d9;
		font-size: 0;
		a {
			display: inline-block;
			vertical-align: top;
			width: 20%;
			font-size: 12px;
			line-height: 17px;
			text-align: center;
			padding-top: 30px;
			height: 20px;
			background-image: url(../assets/nav4.png);
			background-repeat: no-repeat;
			background-size: 24px 600px;
		}
		.index {
			background-position: center 0px;
		}
		.classify {
			background-position: center -300px;
		}
		.carList {
			background-position: center -350px;
		}
		.orderList {
			background-position: center -400px;
		}
		.mine {
			background-position: center -450px;
		}
	}
</style>


