<template>

	<div class="vr" >
		<dl class="vu" v-for="(list,index) in good" :key="index">
			<dt class="vv">{{list.info}}</dt>
			<dd class="vw">
				<aimg v-for="(clag,index) in list.total" :key="index" :seee="true" anclass="vx" inclass="vy" spclass="vz" :imgurl="clag.imgurl" :texth="clag.name" ></aimg>
			</dd>
		</dl>
	</div>

</template>

<script type="text/javascript">
	import aimg from '../common/aimg';

	export default {
		name: 'goodz',
		components: {
			aimg
		},
		props:['good'],
		data: function() {
			return {
				
			}
		}
	}
</script>

<style lang="scss">
	.vr {
		min-height: 527px;
		padding-left: 95px;
		padding-bottom: 50px;
		background-color: #FFF;
		-webkit-overflow-scrolling: touch;
		.vu {
			padding-top: 9px;
			.vv {
				height: 19px;
				margin-top: 9px;
				margin-bottom: 9px;
				margin-left: 7%;
				line-height: 19px;
				font-size: 13px;
				color: #666666;
			}
			.vw {
				font-size: 0;
			}
		}
	}
</style>


